import Nav from './Nav'
import Link from 'next/link';
import styled from "styled-components";

const Logo = styled.h1`
    font-size: 4rem;
    margin-left: 2rem;
    position: relative;
    z-index: 2;
    transform: skew(-7deg);
    a {
        background: ${props => props.theme.red};
        padding: 0.5rem 1rem;
        color: white;
        text-transform: uppercase;
        text-decoration: none;
    }
    @media (max-width: 1300px) {
        margin: 0;
        text-align: center;
    }
`

const Header = props => (
    <>
        <div className="bar">
            <Logo>
                <Link href="/">
                    <a>Not Supreme</a>
                </Link>
            </Logo>
            <Nav />
        </div>
        <div className="sub-bar">
            <p>Search</p>
        </div>
        <div>Cart</div>
    </>
)

export default Header;